.navbar {
  display: none;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  width: 100%;
  background-color: black;
  color: #fff;
  text-align: center;
  z-index: 999;
}

.navbar ul {
  list-style: none;
  padding: 0;
  display: flex;
  justify-content: space-around;
  width: 100%;
}

.navbar a {
  text-decoration: none;
  color: #fff;
  font-size: 0.9rem;
}

.navbar a {
  text-decoration: none;
  color: #fff;
  display: inline-block;
  position: relative;
}

.navbar a::before,
.navbar a::after {
  content: "[";
  position: absolute;
  top: 0;
  transition: transform 0.3s ease;
}

.navbar a::after {
  content: "]";
  right: 0;
}

.navbar a:hover::before {
  color: #2ba2a2;
  transform: translateX(-5px);
}

.navbar a:hover::after {
  color: #2ba2a2;
  transform: translateX(5px);
}

